
.about {
    background-color: $bgc-body-pink;
    padding: 60px 0px 40px 0px;
}

.about__toptext {
    margin: 0px 0px 15px 0px;
    color: $label-title-color;

    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;
    text-align: center;
    text-transform: uppercase;

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        font-size: 26px;
        line-height: 1.15;
  }  
     @media screen and (min-width: 1200px) {
         font-size: 30px;
         line-height: 1.13;
         letter-spacing: 0.04em;
  }
}
.about__title {
    text-align: center;
    color: $accent-color;
    align-content: center;

    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.14;
    
    letter-spacing: 0.04em;
    text-transform: uppercase;

        @media screen and (min-width: 768px) and (max-width: 1199px) {
        font-size: 48px;
        line-height: 1.14;
    }  
       @media screen and (min-width: 1200px) {
         font-size: 58px;
         line-height: 1.13;
         letter-spacing: 0.06em;
         margin-bottom: 60px;
    }
}
.about__box {
    margin-top: 32px;
    background-image: url(/src/images/milk1x.png);
    background-repeat: no-repeat;
    background-position: 0px 340px;
    background-size: auto;
    
    @media screen and (min-width: 768px) and (max-width: 1199px) {
    background-image:none;
  }
    @media screen and (min-width: 1200px) {
    background-image: url(/src/images/milk2x.png);
    background-repeat: no-repeat;
    background-position: 0px -20px;
    background-size: 1160px;
  }
}

.about__text-up {
    margin-bottom: 40px;
    color: $primary-color; 
    width: 220px;
    height: 79px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.58;

        @media screen and (min-width: 768px) and (max-width: 1199px) {
        font-size: 14px;
        line-height: 1.78;
        width: 334px;
    }
         @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: 1.75;
        width: 463px;
    } 
}
.about__text {
    margin-bottom: 20px;
    color: $secondary-color;    
    width: 220px;

    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.58;

        @media screen and (min-width: 768px) and (max-width: 1199px) {
        font-size: 14px;
        line-height: 1.78;
        width: 334px;
    }
        @media screen and (min-width: 1200px) {
        font-size: 14px;
        line-height: 1.92;
        width: 463px;
        letter-spacing: 0.04em;
    } 
}
.about__btn {
    background-color: $white-color;
    border-radius: 22px;
    border: none;
    
    width: 176px;
    height: 44px;
    color: $accent-color;

    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    align-items: center;
    text-align: center;
    margin: 0 auto;
    cursor: pointer;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), 
    color .25s cubic-bezier(.4, 0, .2, 1);

    &:hover,
    &:focus {
        background-color: $accent-color;
        color: $white-color;
        .icon__about {
            stroke: $white-color;
        }
    }
    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin-top: 40px;
    }
}
.icon__about {
    stroke: $accent-color;
    transition: stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);
    margin-left: 13px;   
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
    .about__box {
        display: flex;
        justify-content: space-between;
    }
  }
@media screen and (min-width: 1200px) {
    .about__box {
        display: flex;
        justify-content: center;
        margin: 0;
    }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
    .about__box-text {
        margin-left: 32px;
    }
  }
@media screen and (min-width: 1200px) {
    .about__box-text {
        padding-top: 90px;
        padding-right: 121px;
        margin-left: 99px;
    }
  }  

